import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {MoneyExchangePoint,SaveMoneyExchangePoint,ScoreExchangePoint,GetUserLogPointList,SaveScoreExchangePoint} from 'api/user'
export default{
	components:{
		UniLoadMore
	},
	data(){
		return{
			tabs:['余额兑换','积分兑换','点券明细'],
			index: 0,
			state:{},
			data:{},
			value:'',
			list: [],
			page: 1,
			pageCount: 1,
			moreStatus:"",
		}
	},
	watch:{
		value(val){
			if(this.index == 0){
				if(val>this.data.available_balance){
					this.value = this.data.available_balance
				}
			}else if(this.index == 1){
				if(val>this.data.most_point){
					this.value = this.data.most_point
				}
			}
			
		}
	},
	/*滚动加载*/
	onReachBottom(){
		this.page++
		if(this.page<=this.pageCount){
			this.getList(this.page)	
		}	
	},
	methods:{
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
			}
			this.moreStatus = 'loading'
			GetUserLogPointList({
				page:this.page,
				pagesize:15,
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pagecount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.list = this.list.concat(res.data)
				}
			})		
		},
		changeTabs(e){
			this.value = ''
			if(this.index==0){
				this.getData()
			}else if(this.index == 1){
				this.getScoreData()
			}else{
				this.getList()
			}
		},
		getData(){
			MoneyExchangePoint().then(res=>{
				if(res.result){
					this.data = res.data
				}
			})
		},
		getScoreData(){
			ScoreExchangePoint().then(res=>{
				if(res.result){
					this.data = res.data
				}
			})
		},
		//余额兑换
		exchange(){
			if(this.state.userInfo.Money - 1 > 0){
				if(this.value>0){
					SaveMoneyExchangePoint({
						point:this.value
					}).then(res=>{
						if(res.result){
							uni.showToast({
								title:'兑换成功',
								icon:'none'
							})
							this.getData()
						}else{
							uni.showToast({
								title:res.msg,
								icon:'none'
							})
						}
					})
				}else{
					uni.showToast({
						title:'输入兑换数量',
						icon:'none'
					})
				}
			}else{
				uni.showToast({
					title:'余额不足',
					icon:'none'
				})
			}
		},
		//积分兑换
		scoreexchange(){
			
				if(this.value>0){
					SaveScoreExchangePoint({
						scorepoint:this.value
					}).then(res=>{
						if(res.result){
							uni.showToast({
								title:'兑换成功',
								icon:'none'
							})
							this.getScoreData()
						}else{
							uni.showToast({
								title:res.msg,
								icon:'none'
							})
						}
					})
				}else{
					uni.showToast({
						title:'输入兑换数量',
						icon:'none'
					})
				}
		
		}
	},
	onLoad() {
		this.$store.ready(state=>{
			this.state=  state
			this.getData()
		})
	}
}
